#grid {
    display: grid;
    grid-template-columns: 15% auto;
    height: 100vh
}

.link {
    font-size: 1.5rem;
    color: black
}

.link:hover {
    background-color: purple;
    color: white
}

.card:hover {
    border: 1px solid purple;
    cursor: pointer;
}

.br {
    display: none;
}

#sh {
    display: none;
}

#itemgrid {
    display: grid;
    grid-template-columns: 20% auto;
}

@media only screen and (max-width: 600px) {
    .br {
        display: block;
    }
    #itemgrid {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 13% auto;
    }
    #im {
        width: 7rem;
    }
    #grid {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 13% auto;
        height: 100vh;
    }
    #left {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
    .link {
        font-size: 1.2rem;
        color: black
    }
    #sh {
        display: block;
    }
    h3 {
        display: none;
    }
}